ttom-color:transparent;transition:all .2s}nav .navbar-content{display:grid;margin:auto;max-width:1440px;grid-template-columns:repeat(3,1fr);align-items:center;justify-items:stretch}nav .navbar-content .navigates{width:0;justify-self:start}nav .navbar-content .navigates .cover{position:absolute;top:0;left:-120%;z-index:3;width:100vw;height:100vh;background-color:rgba(0,0,0,.3137254902);transition:all .2s}@media screen and (min-width:1024px){nav .navbar-content .navigates .cover{display:none}}nav .navbar-content .navigates .cover.active{left:0}nav .navbar-content .navigates .navlinks{position:absolute;left:0;top:0;z-index:4;display:flex;flex-direction:column;align-items:center;gap:2rem;padding:4rem 1rem 0 1rem;height:100vh;width:300px;background-color:#161616;color:#fff;transform:translateX(-100%);transition:all .2s}@media screen and (min-width:1024px){nav .navbar-content .navigates .navlinks{position:static;height:fit-content;padding:0;width:100%;transform:translateX(0);background:0 0}nav .navbar-content .navigates .navlinks .social-media{display:none}}nav .navbar-content .navigates .navlinks ul{display:flex;flex-direction:column;width:100%;gap:.6rem}@media screen and (min-width:1024px){nav .navbar-content .navigates .navlinks ul{flex-direction:row}}nav .navbar-content .navigates .navlinks .social-media{flex-direction:row;justify-content:space-evenly;gap:.6rem}nav .navbar-content .navigates .navlinks .social-media a{display:block}nav .navbar-content .navigates .navlinks .social-media a:hover svg{width:44px;height:44px}nav .navbar-content .navigates .navlinks .social-media a:hover svg path{fill:#898989;transition:all .2s}nav .navbar-content .navigates .navlinks.active{transform:translateX(0)}nav .navbar-content .navigates .hamburger{background-color:rgba(0,0,0,0);border:none;position:relative;z-index:5;display:flex;flex-direction:column;justify-content:space-between;width:50px;height:44px;padding:7px}@media screen and (min-width:1024px){nav .navbar-content .navigates 