e:2.5rem}}.restaurant-detail h2{font-size:1.5rem;font-weight:600;font-family:inherit;color:#a5a5a5}.restaurant-detail h3{line-height:20px;font-weight:500;color:#a5a5a5}.restaurant-detail h4{font-size:1.2rem;color:#161616}.restaurant-detail h5{font-size:1rem;font-weight:400}.restaurant-detail .restaurant-image{position:sticky;top:0;display:flex;justify-content:center;background-color:#a5a5a5}.restaurant-detail .restaurant-image img{width:100%;max-height:80vh;min-height:30vh;object-fit:cover;object-position:center}.restaurant-detail .restaurant-image .loader{margin:4rem}.restaurant-detail .container{min-height:70vh;position:relative;padding-top:2rem;padding-bottom:2rem;z-index:1;background-color:#fff;display:flex;justify-content:center;align-items:center}.restaurant-detail .container .restaurant-detail{max-width:1200px;margin:auto}.restaurant-detail .container .restaurant-detail .restaurant-name{display:flex;flex-wrap:wrap;align-items:center;column-gap:1rem}.restaurant-detail .container .restaurant-detail .restaurant-rating{display:flex;align-items:center;gap:10px;margin-bottom:.8rem}.restaurant-detail .container .restaurant-detail .restaurant-rating .star{display:flex;align-items:center;gap:2px}.restaurant-detail .container .restaurant-detail .restaurant-desc{margin-bottom:1.2rem}`;
    