import {css} from 'lit'; export default css`*{padding:0;margin:0;box-sizing:border-box;font-family:Poppins,"Arial Narrow",Arial,sans-serif}a{text-decoration:none;color:#000}ol,ul{list-style:none}p{font-size:.9rem;line-height:1.5rem}h1{font-size:4rem}h2{font-family:"Rampart One";font-size:2.5rem;font-weight:400;text-transform:uppercase;line-height:3rem}main{min-height:100vh}@media screen and (min-width:768px){p{font-size:1rem;line-height:1.5rem}h1{font-size:6rem}h2{font-size:3rem;line-height:3rem}}@media screen and (min-width:1024px){h2{font-size:3.5rem}}.skip-to-content button{position:absolute;background-color:#e88f2a;color:#fff;left:10px;top:-100px;z-index:10;padding:1rem;transition:all .3s}.skip-to-content button:focus{top:0}.container{padding-left:1rem;padding-right:1rem}.block{display:block}.margin-section{margin-bottom:4rem}.margin-bottom{margin-bottom:1rem}.logo{font-weight:700;font-size:1.5rem;color:#fff}.logo span{color:#e88f2a}.section-header{display:flex;flex-direction:column;align-items:center;margin-bottom:1rem}.section-header>h2,.section-header>p{text-align:center}.section-header>p{max-width:768px}.overflow-hidden{overflow:hidden}.hidden{display:none!important}.invisible{visibility:hidden!important}.loader{display:block;width:100px;height:100px;border-radius:50%;border:6px solid #e88f2a;animation:spinner 1.2s linear infinite;border-color:transparent #e88f2a transparent #e88f2a}.small-loader{display:block;width:25px;height:25px;border-radius:50%;border:3px solid #e88f2a;animation:spinner 1.2s linear infinite;border-color:transparent #e88f2a transparent #e88f2a}@media screen and (min-width:768px){.margin-section{margin-bottom:6rem}}@media screen and (min-width:1024px){.margin-section{margin-bottom:7rem}}@keyframes spinner{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}.popup-element{position:fixed;z-index:1;right:20px;bottom:20px}.popup-element .popup-container{display:flex;flex-direction:column-reverse;gap:.75rem}.popup-element .popup-container .popup-box{display:flex;flex-direction:column;gap:16px;width:220px;border-radius:10px;padding:.8rem 1rem;background-color:#161616;border:1px solid #303030;box-shadow:0 0 12px -2px rgba(0,0,0,.5019607843)}.popup-element .popup-container .popup-box p{font-size:.8rem;color:#e88f2a;word-wrap:break-word}.popup-element .popup-container .popup-box button-element{align-self:flex-end}.popup-element .popup-container .popup-box button-element span{display:block;font-size:16px;margin:0 -2px}`;
    