f}:host h3{color:#161616;font-size:1.2rem}:host .menus-content .menu-container{display:grid;grid-template-columns:repeat(auto-fill,15rem);row-gap:1rem;justify-content:center;margin-bottom:2rem}:host .menus-content .menu-container .menu-item{margin:0 .5rem;box-shadow:0 0 9px -2px rgba(0,0,0,.5);padding:1rem;border-radius:7px}:host .menus-content .menu-container .menu-item .menu-image{aspect-ratio:1/1;width:100%;background-color:#a5a5a5;margin-bottom:1rem}`;
    