import {css} from 'lit'; export default css`*{padding:0;margin:0;box-sizing:border-box;font-family:Poppins,"Arial Narrow",Arial,sans-serif}a{text-decoration:none;color:#000}ol,ul{list-style:none}p{font-size:.9rem;line-height:1.5rem}h1{font-size:4rem}h2{font-family:"Rampart One";font-size:2.5rem;font-weight:400;text-transform:uppercase;line-height:3rem}main{min-height:100vh}@media screen and (min-width:768px){p{font-size:1rem;line-height:1.5rem}h1{font-size:6rem}h2{font-size:3rem;line-height:3rem}}@media screen and (min-width:1024px){h2{font-size:3.5rem}}.skip-to-content button{position:absolute;background-color:#e88f2a;color:#fff;left:10px;top:-100px;z-index:10;padding:1rem;transition:all .3s}.skip-to-content button:focus{top:0}.container{padding-left:1rem;padding-right:1rem}.block{display:block}.margin-section{margin-bottom:4rem}.margin-bottom{margin-bottom:1rem}.logo{font-weight:700;font-size:1.5rem;color:#fff}.logo span{color:#e88f2a}.section-header{display:flex;flex-direction:column;align-items:center;margin-bottom:1rem}.section-header>h2,.section-header>p{text-align:center}.section-header>p{max-width:768px}.overflow-hidden{overflow:hidden}.hidden{display:none!important}.invisible{visibility:hidden!important}.loader{display:block;width:100px;height:100px;border-radius:50%;border:6px solid #e88f2a;animation:spinner 1.2s linear infinite;border-color:transparent #e88f2a transparent #e88f2a}.small-loader{display:block;width:25px;height:25px;border-radius:50%;border:3px solid #e88f2a;animation:spinner 1.2s linear infinite;border-color:transparent #e88f2a transparent #e88f2a}@media screen and (min-width:768px){.margin-section{margin-bottom:6rem}}@media screen and (min-width:1024px){.margin-section{margin-bottom:7rem}}@keyframes spinner{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}.item-container{display:flex;position:relative;margin-top:.5rem;flex-direction:column;border-radius:25px;box-shadow:0 0 10px -5px #000}.item-container .restaurant-cover{display:flex;position:relative;justify-content:center;width:100%;height:170px;border-radius:25px;overflow:hidden;border-bottom:5px solid #e88f2a;background-color:gray}.item-container .restaurant-cover img{height:100%;width:100%;object-fit:cover}.item-container .restaurant-detail{height:260px;position:relative;padding:1rem 1rem 1.5rem;display:flex;flex-direction:column;justify-content:space-between}.item-container .restaurant-detail a{display:block;width:fit-content}.item-container .restaurant-detail:before{content:"";position:absolute;z-index:1;margin-top:-1rem;left:50%;transform:translateX(-50%);width:0;height:0;border:10px solid transparent;border-top:10px solid #e88f2a}.item-container .restaurant-detail .restaurant-detail-text h4{font-weight:400}.item-container .restaurant-detail .restaurant-detail-text h4 span{display:inline-block;color:#e88f2a;margin-right:5px;font-weight:600}.item-container .restaurant-detail .restaurant-detail-text p{display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden;width:100%;height:100%;margin-bottom:5px}.item-container .restaurant-city{background-color:#e88f2a;color:#fff;position:absolute;top:0;left:25px;padding:.4rem .6rem;border-bottom-left-radius:10px;border-bottom-right-radius:10px;border-top-right-radius:5px;transform:translateY(-8px)}.item-container .restaurant-city::after{content:"";position:absolute;left:-6px;top:.5px;height:7.5px;width:5.5px;background-color:#844d0e;border-top-left-radius:50%;border-top-right-radius:50%}.item-container .restaurant-city::before{content:"";position:absolute;left:-6px;top:0;height:8px;width:6px;background-color:#e88f2a;border-top-left-radius:50%}`;
    